html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  background-color: #191717;
}
body.fixed-image {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #D1A021;
  line-height: 2.5rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 4rem 0;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2.2rem;
  text-transform: uppercase;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: rgba(33, 24, 43, 0.6352941176);
}
.header.stick {
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.title-nav {
  color: #6F4059;
  display: block;
  letter-spacing: 2px;
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2.2rem;
  font-weight: 900;
}
@media (min-width: 768px) {
  .title-nav {
    margin: 0;
    font-size: 3.2rem;
    padding: 1rem;
    text-align: center;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  margin-bottom: 1.5rem;
  color: rgba(255, 238, 255, 0.9333333333);
  font-size: 2rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: rgba(233, 160, 43, 0.8352941176);
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(219, 38, 38);
  background: linear-gradient(270deg, rgba(219, 38, 38, 0.4) 0%, rgba(205, 0, 255, 0.4) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 10;
}
.contenido-video h2 {
  color: rgba(255, 238, 255, 0.9333333333);
  font-size: 3.6rem;
  margin-bottom: 1rem;
}
.contenido-video p {
  background-color: rgba(144, 12, 63, 0.5725490196);
  display: inline-block;
  padding: 1rem 2rem;
  font-weight: 700;
  margin: 0;
}

.sobre-festival {
  padding: 4rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festvial {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-festvial {
    margin-top: 0;
  }
}
.contenido-festvial h2 {
  margin-bottom: 1.5rem;
  color: #6F4059;
}
.contenido-festvial .fecha {
  font-weight: 500;
  font-size: 1.8rem;
  color: #D1A021;
}
.contenido-festvial .info-festival {
  color: rgba(255, 238, 255, 0.9333333333);
  font-size: 1.5rem;
  font-weight: 300;
}

.lineup {
  background-color: rgba(144, 12, 63, 0.5725490196);
  padding: 5rem 0;
}
.lineup h3,
.lineup P {
  text-align: center;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 3rem 0;
}

.escenario-contenedor {
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .escenario-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenario-contenedor p {
  color: #8A7999;
}
.escenario-contenedor .escenario {
  padding: 2rem 0;
  margin-top: 3rem;
  border-radius: 1.5rem;
}
@media (min-width: 768px) {
  .escenario-contenedor .escenario {
    margin-top: 0;
  }
}
.escenario-contenedor .escenario p:after {
  content: "";
  display: block;
  width: 7rem;
  height: 7rem;
  background-size: 4rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
  border: none;
}

.horario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.horario li {
  padding: 1.5rem;
  font-weight: 700;
}
.horario li span {
  font-weight: 400;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .horario {
    font-size: 1.4rem;
  }
}

.principal .nombre-escenario:after {
  background-image: url(../img/icono_mixis.png);
}

.alterno .nombre-escenario:after {
  background-image: url(../img/icono_volumen.png);
}

.bg-blanco {
  background-color: rgba(255, 238, 255, 0.9333333333);
}
.bg-blanco li:nth-child(odd) {
  background-color: rgba(255, 202, 255, 0.9333333333);
}

.bg-oscuro {
  background-color: #21182B;
  color: rgba(255, 238, 255, 0.9333333333);
}
.bg-oscuro li:nth-child(odd) {
  background-color: #1c1424;
}

.galeria {
  padding: 8rem 0;
}

.title-galeria {
  color: rgba(255, 238, 255, 0.9333333333);
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2.5rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2.5rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(132, 54, 191, 0.359);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-close {
  width: 3.5rem;
  height: 3.5rem;
  text-transform: uppercase;
  font-weight: 900;
  border-radius: 50%;
  background-color: rgba(144, 12, 63, 0.5725490196);
  display: grid;
  place-items: center;
}
.btn-close:hover {
  cursor: pointer;
}

.tickets {
  padding: 4rem 0;
  background-color: rgba(144, 12, 63, 0.5725490196);
}
.tickets h3 {
  text-align: center;
}
@media (min-width: 768px) {
  .tickets .contenedor-tickets {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase-header, .pase-footer {
  padding: 0.8rem;
}
.pase-header p, .pase-footer p {
  text-align: center;
  font-weight: 600;
  margin: 0;
}
.pase-header {
  background-color: rgba(33, 24, 43, 0.6352941176);
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  color: rgba(1, 1, 1, 0.7764705882);
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li,
.pase-body ul :nth-child(odd) {
  background-color: rgba(255, 238, 255, 0.453);
  padding: 1rem;
}
.pase-footer {
  background-color: rgba(33, 24, 43, 0.6352941176);
  border-radius: 5%;
  margin-bottom: 2.5rem;
}
.pase-footer p {
  font-size: 2.2rem;
  border-radius: 5%;
}

h4 {
  display: block;
  margin: 0 auto;
  color: rgba(255, 238, 255, 0.9333333333);
  text-align: center;
  width: 25rem;
  padding: 1.5rem;
  font-size: 1.8rem;
  background-color: rgba(111, 64, 89, 0.6666666667);
  border-radius: 1rem;
  text-transform: uppercase;
}

.nombre-pase {
  text-align: center;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  padding: 1.5rem 0;
}

.btn-comprar {
  display: block;
  margin: 0 auto;
  margin-top: 5rem;
  text-transform: uppercase;
  padding: 1rem;
  border-radius: 1rem;
  border: none;
  background-color: rgba(255, 238, 255, 0.453);
  color: rgba(33, 24, 43, 0.6352941176);
}
.btn-comprar:hover {
  cursor: pointer;
  background-color: rgba(33, 24, 43, 0.6352941176);
  color: rgba(255, 238, 255, 0.453);
}

.site-footer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5rem;
  padding: 1.5rem;
  width: 100%;
  background-color: rgba(33, 24, 43, 0.6352941176);
}
.site-footer p {
  font-size: 1.6rem;
  text-align: center;
  margin: 0;
}